body {
    margin: 0;
    padding: 0;
    background-color: #000;
    color: #fff;
    font-family: Monospace;
    font-size: 13px;
    line-height: 24px;
}

a {
    color: #ff0;
    text-decoration: none;
    &:hover {
        text-decoration: underline;
    }
}

button {
    cursor: pointer;
    text-transform: uppercase;
}

canvas {
    display: block;
}

#info {
	position: absolute;
	top: 0px;
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	text-align: center;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	pointer-events: none;
	z-index: 1; /* TODO Solve this in HTML */
}

a, button, input, select {
	pointer-events: auto;
}

.dg.ac {
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	z-index: 2 !important; /* TODO Solve this in HTML */
}

#message-container {
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 10000;
	background-color: rgba(0, 0, 0, 0.5);
	color: white;
	width: 100%;
	height: 100px;
	text-align: center;
	font-size: 21px;
	line-height: 100px;
	letter-spacing: 0.1em;
}

#mode-button {
	position: absolute;
	top: 10px;
	left: 30px;
	padding: 8px 32px;
	border-radius: 16px;
	color: white;
	line-height: 16px;
	font-size: 16px;
	border: none;
	outline: 0;
}

.edit-mode {
	background-color: green;
}

.view-mode {
	background-color: blue;
}

#status {
	color: white;
}